.breadcrumb {
  display: flex;
  align-items: center;
  white-space: nowrap;
  padding: 2px;
  border-bottom: 1px solid #e2e8f0;
  justify-content: center;
}

.breadcrumb-item {
  display: inline-flex;
  align-items: center;
}

.breadcrumb-link {
  display: flex;
  align-items: center;
  font-size: 14px;
  color: #6b7280;
  text-decoration: none;
  transition: color 0.2s ease-in-out;
}

.breadcrumb-link:hover,
.breadcrumb-link:focus {
  color: #2563eb;
}

.breadcrumb-icon {
  flex-shrink: 0;
  margin-right: 0.75rem;
  height: 1rem;
  width: 1rem;
}

.breadcrumb-divider {
  flex-shrink: 0;
  margin-right: 0.5rem;
  overflow: visible;
  height: 1rem;
  width: 1rem;
  color: #9ca3af;
}

.breadcrumb-current {
  display: inline-flex;
  align-items: center;
  font-size: 14px;
  font-weight: 600;
  color: #1f2937;
  max-width: 100%;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  margin-left: 0.5rem;
}
